body {
  font-family: sans-serif;
}

.loader {
  margin: 15px auto 0;
  display: block;
}

loader.hidden, .breed-select.hidden {
  display: none;
}

.cat-info {
  width: 100%;
  gap: 20px;
  margin-top: 10px;
  display: flex;
}

.cat-img {
  width: 400px;
  box-shadow: 5px 5px 6px 2px rgba(0, 0, 0, .3);
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

